.contacts {
  padding-block: 96px;

  @include onTablet {
    padding-block: 120px;
  }

  &__content {
    display: flex;
    flex-direction: column;
    gap: 48px 0;

    @include onTablet {
      @include pageGrid;
    }
  }

  &__form {
    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 24px 0;

    @include onTablet {
      grid-column: span 3;
    }

    @include onDesktop {
      grid-column: 8 / 12;
    }
  }

  &__contact-title {
    margin: 0 0 8px;
    color: $contact-title-color;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
  }

  &__contact-detail {
    text-decoration: none;
    color: $contact-detail-color;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;

    transition: color $effectDuration;

    &:hover {
      color: #D12D35;
    }
  }
}