.categories {
  padding-block-start: 96px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__wrapper {
    display: grid;
    row-gap: 40px;
    margin-bottom: 40px;

    @include onTablet {
      row-gap: 64px;
    }
  }

  &__button {
    @include pageGrid;

    &-wrapper {
      grid-column: -1 / 1;

      @include onTablet {
        grid-column: 2 / 6;
      }

      @include onDesktop {
        grid-column: 5 / 9;
      }
    }
  }
}

.category {
  &__photos {
    @include pageGrid;
    margin-bottom: 16px;
  }

  &__link {
    position: relative;
    overflow: hidden;

    &--wide {
      @include onTablet {
        grid-column: span 4;
      }

      @include onDesktop {
        grid-column: span 8;
      }
    }

    &--square {
      height: 0;
      padding-bottom: 100%;

      @include onTablet {
        grid-column: span 2;
      }

      @include onDesktop {
        grid-column: span 4;
      }
    }
  }

  &__photo {
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include hover(transform, scale(1.1));

    &--position--right--75 {
      object-position: 75%;
    }

    &--position--right--92 {
      object-position: 92%;
    }
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 140%;

    @include onTablet {
      font-size: 24px;
    }
  }
}