.how-to-buy {
  @include pageGrid;
  padding-block-start: 96px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__title {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__content {
    grid-column: 1 / 3;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: span 5;
    }
  }

  &__benefits {
    display: grid;
    row-gap: 16px;

    margin: 0;
    margin-bottom: 40px;
    padding: 0;
    list-style: none;
    font-weight: bold;
  }

  &__description {
    margin: 0;
    line-height: 27px;
  }
}