.form {
  display: flex;
  flex-direction: column;
  gap: 16px;

  &__field {
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
    padding: 14px 16px;
    outline: none;
    border-radius: 40px;
    background-color: $form-bg-color;
    border: 1px solid transparent;
    font-size: 14px;
    line-height: 20px;

    &:-webkit-autofill {
      transition: background-color 999999s;
      -webkit-text-fill-color: $main-text-color;
    }

    &::placeholder {
      font-family: 'Inter', sans-serif;
      font-weight: 400;
      font-size: 14px;
      line-height: 20px;
      color: $form-placeholder-color;
    }

    &:hover {
      border-color: #D5D7DE;
    }

    &:focus {
      border-color: $main-text-color;
    }
  }

  &__field--textarea {
    min-height: 148px;
    border-radius: 16px;
    resize: none;
    margin-bottom: 16px;
  }
}