.header {
  background-image: url(../images/header/header-bg-mobile.png);
  background-size: cover;
  background-position: center;

  @include onTablet {
    background-image: url(../images/header/header-bg.png);
  }

  &__content {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__waves {
    height: 27px;
    width: 32px;
    margin-bottom: 16px;
    background-image: url(../images/header/sound-waves-mobile.svg);
    background-size: cover;

    @include onTablet {
      height: 76px;
      width: 63px;
      margin-bottom: 40px;
      background-image: url(../images/header/sound-waves-tablet.svg);
    }

    @include onDesktop {
       margin-bottom: 32px;
    }
  }

  &__title {
    margin: 0;
    font-style: italic;
    font-weight: 800;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 40px;
    }

    @include onDesktop {
      font-size: 48px;
    }
  }

  &__bottom {
    padding-bottom: 94px;

    @include onTablet {
      padding-bottom: 40px;
    }

    @include onDesktop {
      padding-bottom: 64px;
    }
  }
}