.recommended {
  padding-block-start: 64px;

  @include onTablet {
    padding-block-start: 120px;
  }

  &__products {
    @include pageGrid;
    row-gap: 48px;

    @include onTablet {
      row-gap: 40px;
    }
  }

  &__product {
    grid-column: 1 / -1;

    @include onTablet {
      grid-column: 2 / -2;
    }

    @include onDesktop {
      grid-column: span 4;
    }
  }
}